import React from 'react'
import {TabBar, WingBlank} from 'antd-mobile'
import './Detail.css'

const Detail1 = () => {
    return (
        <div>
            <TabBar>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-fanhuijiantou'></i>                           
                        }
                        onPress={ () =>
                            window.history.back(-1)
                         }
                    >
                    </TabBar.Item>
            </TabBar>
            <WingBlank>
                <img src='https://img.alicdn.com/imgextra/i1/13908605/O1CN01U0POQM2DR8lRCE0aU_!!0-saturn_solar.jpg_220x220.jpg_.webp' alt='' style={{width:'100%',height:'350px'}} />
                <p>匹诺曹系列卡通绣花羊羔毛外套女中长款冬季韩版潮ins加厚大衣</p>
                <h2>￥258</h2>
            </WingBlank>
            
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                        unselectedTintColor="#949494"
                        tintColor="red"
                        barTintColor="white"
                    >
                        <TabBar.Item
                            title="店铺"
                            key="marker"
                            icon={
                                <i className='iconfont icon-dianpu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu1'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="客服"
                            key="marker"
                            icon={
                                <i className='iconfont icon-kefu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-kefu1'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="收藏"
                            key="marker"
                            icon={
                                <i className='iconfont icon-shoucang'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-collection-b'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="加入购物车"
                            key="marker"
                            icon={
                                <i className='iconfont icon-jiarugouwuche'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="立即购买"
                            key="marker"
                            icon={
                                <i className='iconfont icon-lijigoumai1'></i>                           
                            }
                        ></TabBar.Item>
                </TabBar>
            </div>
        </div>
    )
}

export default Detail1