import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0}
            } >
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="orangered"
                    barTintColor="white"
                    tabBarPosition="bottom"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouyeshouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-icon4'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-ai66'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-weibiaoti--'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-tianchongxing-'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname==='/Friend' }
                        onPress={ () => {
                            this.props.history.push('/Friend')
                        } }
                    >
                        
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
                        selectedIcon={ 
                            <i className='iconfont icon-wodedangxuan'></i>
                         }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname==='/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                    >
                        
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);
