import React from 'react'
import {TabBar, WingBlank} from 'antd-mobile'
import './Detail.css'

const Details = () => {
    return (
        <div>
            <TabBar>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-fanhuijiantou'></i>                           
                        }
                        onPress={ () =>
                            window.history.back(-1)
                         }
                    >
                    </TabBar.Item>
            </TabBar>
            <WingBlank>
                <img src='https://img.alicdn.com/imgextra/i2/116196158/O1CN0121lY8G1vMPaj8M2YM_!!0-saturn_solar.jpg_220x220.jpg_.webp' alt='' style={{width:'100%',height:'350px'}} />
                <p>安酒月毛衣套头慵懒风2020初秋季新款条纹宽松外穿上衣针织衫女装</p>
                <h2>￥139</h2>
            </WingBlank>
            
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                        unselectedTintColor="#949494"
                        tintColor="red"
                        barTintColor="white"
                    >
                        <TabBar.Item
                            title="店铺"
                            key="marker"
                            icon={
                                <i className='iconfont icon-dianpu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu1'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="客服"
                            key="marker"
                            icon={
                                <i className='iconfont icon-kefu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-kefu1'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="收藏"
                            key="marker"
                            icon={
                                <i className='iconfont icon-shoucang'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-collection-b'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="加入购物车"
                            key="marker"
                            icon={
                                <i className='iconfont icon-ic_jiarugouwuche'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="立即购买"
                            key="marker"
                            icon={
                                <i className='iconfont icon-lijigoumai1'></i>                           
                            }
                        ></TabBar.Item>
                </TabBar>
            </div>
        </div>
    )
}

export default Details